<template>
	<div class="cartcontrol">
		<transition name="move">
			<div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="removeFood">
				<span class="inner icon-remove_circle_outline"></span>
			</div>
		</transition>
		<div class="cart-count" v-show="food.count>0">{{food.count}}</div>
		<div class="cart-increase icon-add_circle" @click.stop.prevent="addCart($event)"></div>
	</div>
</template>

<script>
import Vue from 'vue';
export default{
	props: {
		food: {
			type: Object
		}
	},
	methods: {
		addCart(event) {
			if (!event._constructed) {
				return;
			}
			if (!this.food.count) {
				Vue.set(this.food, 'count', 1);
			} else {
				this.food.count ++;
			}
			this.$emit('add', event.target);
		},
		removeFood(event) {
			if (!event._constructed) {
				return;
			}
			if (this.food.count) {
				this.food.count --;
			}
		}
	}
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
	.cartcontrol
		font-size:0
		.cart-decrease
			display:inline-block
			padding:6px
			transform: translate3d(0, 0, 0)
			opacity:1
			.inner
				display:inline-block
				line-height:24px
				font-size:24px
				color:rgb(0,160,220)
				transition: all 0.4s linear
				transform:rotate(0)
			&.move-enter-active, &.move-leave-active
				transition:all 0.4s linear
			&.move-enter,&.move-leave-to
				opacity:0
				transform: translate3d(30px, 0, 0)
				.inner
					transform:rotate(180deg)
		.cart-count
			display:inline-block
			font-size:10px
			color:rgb(147,153,159)
			line-height:24px
			width:12px
			padding:6px
			text-align:center
			vertical-align:top
		.cart-increase
			font-size:24px
			line-height:24px
			display:inline-block
			padding:6px
			color:rgb(0,160,220)
</style>


